.container {
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: calc(100vh - 228px);
  margin-bottom: 20px;
  position: relative;
  /* height: 600px; */
  /* overflow-y: scroll; */
}

.title {
  font-weight: bold;
  text-align: start;
  margin-bottom: 16px;
}

.progress-bar-container {
  width: 100%;
  margin-bottom: 16px;
  background-color: #c5ccff;
}

.progress-bar {
  height: 4px;
  background-color: #5569ff;
}

.profile-control {
  margin: 16px 0;
}

.navigation-buttons {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  /* background-color: white; */
  padding: 16px;
  display: flex;
  justify-content: center;
  gap: 16px;
}


.inputfields {
  width: 50%;
}

.donebutton {
  position: sticky;
  bottom: 0;   
  right: 0;
  padding: 10px;
  background-color: #fff;
  display: flex;
  justify-content: flex-end;                                     
}

/* Responsive Styles */
@media (max-width: 768px) {
  .title {
    font-size: 1.5rem;
  }

  .progress-bar {
    width: 50%;
  }

  .navigation-buttons {
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }
}
